.dd {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}

.dd-list {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
}

.dd-list .dd-list {
  padding-left: 30px;
}

.dd-collapsed .dd-list {
  display: none;
}

.dd-item,
.dd-empty,
.dd-placeholder {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 13px;
  line-height: 20px;
  min-height: 30px;
}

.dd-placeholder,
.dd-empty {
  margin: 0px 0;
  padding: 0;
  min-height: 30px;
  background: rgba(245, 245, 246, 0.3);
  border: 1px dotted #ebebeb;
  box-sizing: border-box;
}

.dd-empty {
  border: 1px dashed #bbb;
  min-height: 100px;
  background-color: #e5e5e5;
}

.dd-handle,
.dd-content {
  display: block;
  margin: 0px 0;
  padding: 5px 10px;
  background: #fafafa;
  box-sizing: border-box;
  border: 1px solid #ebebeb;
  transition: all 300ms ease-in-out;
}

.dd-handle .slug {
  color: #ccc;
  font-size: 10px;
  margin-left: 20px;
  position: relative;
  top: -1px;
}

.dd-handle .actions {
  margin-left: 20px;
}

.dd-content:hover {
  background: #fff;
}

.dd-item button {
  overflow: hidden;
}

.dd-item > button {
  display: block;
  position: relative;
  cursor: pointer;
  float: left;
  width: 25px;
  height: 20px;
  margin: 5px 0;
  padding: 0;
  text-indent: 100%;
  white-space: nowrap;
  overflow: visible;
  border: 0;
  background: transparent;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  font-weight: bold;
  margin-right: 10px;
  outline: 0;
}

.dd-item > button:after {
  content: '';
  display: block;
  position: absolute;
  right: 0px;
  top: -5px;
  bottom: -6px;
  width: 1px;
  background: #ebebeb;
}

.dd-item > button:before {
  content: '+';
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  text-indent: 0;
}

.dd-item > button[data-action="collapse"]:before {
  content: '-';
}

.dd-item .actions {
  right: 0px;
  top: 0px;
  position: absolute;
  padding: 6px;
  background: #ddd;
}

.dd-dragel {
  position: absolute;
  pointer-events: none;
  z-index: 9999;
}

.dd-dragel > .dd-item .dd-handle, .dd-dragel > .dd-item .dd-content {
  margin-top: 0;
}

.dd-dragel .dd-handle, .dd-dragel .dd-content {
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.1);
}

